<template>
  <div class="container">
    <!-- SIDEBAR -->
    <div class="sidebar">
      <h2><span class="accent">|</span> CRUD OPERATIONS</h2>
      <img
        :src="avatarImage"
        alt="Profile"
        class="avatar"
      />
      <h3>Karthi Madesh</h3>
      <p class="role">Admin</p>
      <ul>
        <li @click="goToDashboard">üè† Home</li>
        <li>üìò Course</li>
        <li class="active" @click="goToStudents">üéì Students</li>
        <li @click="goToPayments">üí∞ Payment</li>
        <li>üìä Report</li>
        <li>‚öôÔ∏è Settings</li>
      </ul>
      <button class="logout" @click="logout">Logout</button>
    </div>

    <!-- MAIN CONTENT -->
    <div class="main">
      <div class="header">
        <h2>Students List</h2>
        <button @click="addStudent">Add New Student</button>
      </div>

      <table>
        <thead>
          <tr>
            <th>Name</th>
            <th>Email</th>
            <th>Phone</th>
            <th>Enroll Number</th>
            <th>Date of Admission</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(student, index) in students" :key="index">
            <td>
              <img :src="student.avatar" class="avatar-small" />
              <input v-if="student.editing" v-model="student.name" />
              <span v-else>{{ student.name }}</span>
            </td>
            <td>
              <input v-if="student.editing" v-model="student.email" />
              <span v-else>{{ student.email }}</span>
            </td>
            <td>
              <input v-if="student.editing" v-model="student.phone" />
              <span v-else>{{ student.phone }}</span>
            </td>
            <td>
              <input v-if="student.editing" v-model="student.enrollNumber" />
              <span v-else>{{ student.enrollNumber }}</span>
            </td>
            <td>
              <input v-if="student.editing" v-model="student.date" />
              <span v-else>{{ student.date }}</span>
            </td>
            <td>
              <button v-if="student.editing" @click="saveStudent(index)">üíæ</button>
              <button v-else @click="editStudent(index)">‚úèÔ∏è</button>
              <button @click="deleteStudent(index)">üóëÔ∏è</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      avatarImage:
        "data:image/jpeg;base64,",
      students: [
        {
          name: "John Doe",
          email: "john@example.com",
          phone: "123456789",
          enrollNumber: "ENR001",
          date: "2023-09-01",
          avatar: "",
          editing: false
        },
        {
          name: "Jane Smith",
          email: "jane@example.com",
          phone: "987654321",
          enrollNumber: "ENR002",
          date: "2023-09-02",
          avatar: "",
          editing: false
        }
      ]
    };
  },
  created() {
    this.students.forEach(student => (student.avatar = this.avatarImage));
  },
  methods: {
    goToDashboard() {
        this.$router.push("/dashboard");
    },
    goToPayments() {
        this.$router.push("/payments");
    },
    goToStudents() {
      this.$router.push("/students");
    },
    logout() {
      this.$router.push("/");
    },
    addStudent() {
      const newStudent = {
        name: "New Student",
        email: "new@student.com",
        phone: "000000000",
        enrollNumber: "ENRXXX",
        date: new Date().toISOString().slice(0, 10),
        avatar: this.avatarImage,
        editing: false
      };
      this.students.push(newStudent);
    },
    editStudent(index) {
      this.students[index].editing = true;
    },
    saveStudent(index) {
      this.students[index].editing = false;
    },
    deleteStudent(index) {
      if (confirm("Are you sure you want to delete this student?")) {
        this.students.splice(index, 1);
      }
    }
  }
};
</script>

<style scoped>
.container {
  display: flex;
}

.sidebar {
  background: #f5f5f5;
  width: 200px;
  padding: 20px;
  text-align: center;
  height: 100vh
}

.accent {
  color: #facc15;
  margin-right: 5px;
}

.avatar {
  border-radius: 50%;
  width: 100px;
  margin: 20px 0 10px;
}

.avatar-small {
  width: 30px;
  border-radius: 50%;
  margin-right: 8px;
  vertical-align: middle;
}

.role {
  color: orange;
  font-size: 14px;
  margin-bottom: 20px;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  padding: 10px;
  text-align: left;
  cursor: pointer;
}

li.active,
li:hover {
  background-color: #facc15;
  color: white;
  border-radius: 5px;
}

.logout {
  margin-top: 20px;
  padding: 10px;
  width: 100%;
  background: none;
  border: 1px solid #ccc;
  cursor: pointer;
}

.main {
  flex: 1;
  padding: 20px;
  background: #fff;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
  background: white;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 0 10px #ddd;
}

th,
td {
  padding: 12px;
  text-align: left;
}

th {
  background: #facc15;
  color: white;
}

td button {
  margin-right: 5px;
  background: none;
  border: none;
  cursor: pointer;
}
</style>
